import main from '../css/main.less';
import compileTemplate from '../frontend-modules/simple-compile-template/simple-compile-template.js';
import appendHTML from '../frontend-modules/append-html/append-html.js';

import FacebookSVG from  "../img/social_facebook.svg";
import TwitterSVG from  "../img/social_twitter.svg";
import YoutubeSVG from  "../img/social_youtube.svg";
import ArrowSVG from  "../img/arrow-right.svg";

var social_images = [
    {
        source: FacebookSVG,
        title: "Facebook"
    },
    {
        source: YoutubeSVG,
        title: "Facebook"
    },
    {
        source: TwitterSVG,
        title: "Facebook"
    },
];


var link_template = '<a href="#" class="socal-overlay__icon socal-overlay__icon--disabled"><img src="${scope.source}" title="${scope.title}"></a>';


document.querySelector('#arrow-right').src = ArrowSVG;


social_images.forEach(
    entry =>
        appendHTML(
            document.querySelector('.social-overlay'),
            compileTemplate(link_template, entry)
        )
);


